<template>
	<div class="book-chapter">
		<div class="login-header">
			<div @click="routerGo" class="back">
				<i></i>
				<span>返回</span>
			</div>
			
			<h1>章节</h1>
			
			<router-link to="/">
				<span class="login-home">
					<i></i>
				</span>
			</router-link>
		</div>
		
		<div class="chapter-top">
			<h3>目录</h3>
			<div>共{{chapterlist.length}}章</div>
			<span v-if="reverse" @click="reverseOrder">倒序</span>
			<span v-else @click="reverseOrder">正序</span>
		</div>
		
		<div class="chapter-count" v-for="(b, index) in chapterlist" :key="index">
			<router-link :to="/reading/ + b.title + '/' + encodeURIComponent(b.link)">{{b.title}}</router-link>
		</div>
	</div>
</template>

<script>
	export default {
		name:"BookChapter",
		data(){
			return{
				chapterlist:[],
				reverse: true,
				link:[]
			}
		},
		beforeRouteEnter(to,from,next){
			next(vm => {
				vm.axios.get(vm.ChapterId).then(c => {
					vm.chapterlist = c.data.mixToc.chapters
					console.log(vm.chapterlist)
				})
			})
		},
		methods:{
			routerGo(){
				this.$router.go(-1);
			},
			reverseOrder(){
				this.reverse = !this.reverse 
				//颠倒数组
				this.chapterlist.reverse()
			}
		},
		computed:{
			ChapterId(){
				let urls = `api/mix-atoc/${this.$route.params.id}?view=chapters`
				return urls
			},
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	.book-chapter{
		background-color: #fff;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
	}
	
	.login-header{
		width: 100%;
		height: 2.75rem;
		background-color: #b93321;
		color: #fff;
		display: block;
	
		.back{
			display: inline-block;
			text-align: center;
			height: 100%;
			width: 60px;
			float: left;
			
			i{
				display: inline-block;
				background: url(../../public/img/arror-back.png) no-repeat center;
				background-size: 125%;
				height: 100%;
				width: 0.9375rem;
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
				font-size: 12px;
				margin-left: 2px;
				color:#fff;
			}
		}
		
		h1{
			width: 70%;
			display: inline-block;
			text-align: center;
			line-height: 2.75rem;
		}
		
		.login-home{
			background: url(../../public/img/home.png) no-repeat center;
			background-size: 35%;
			height: 100%;
			width: 14%;
			float: right;
			display: inline-block;
		}
		
	}
	
	.chapter-top{
		width: 100%;
		height: 2.9375rem;
		padding: 0.9375rem 1.25rem;
		line-height: 1rem;
		
		h3{
			color: #222;
			font-size: 0.875rem;
			font-weight: 700;
			float: left;
			margin-right: 0.3125rem;
		}
		div{
			float: left;
			color: #999;
			font-size: 0.75rem;
		}
		span{
			float: right;
			font-size: 0.9375rem;
		}
	}
	
	.chapter-count{
		height: 3.125rem;
		width: 100%;
		padding: 0 1.25rem;
		font-size: 0.75rem;
		
		a{
			color: #999;
			line-height: 3.125rem;
		}
	}
</style>
